

import "./index.scss";
import React, {Component}  from "react";
import {Route, Switch,Redirect} from "react-router-dom";
import {Badge,Stepper,Checkbox,Toast} from "antd-mobile";

import store from '../../store';
import {getGoodsCar,addReduce,getGoodsCount,delOne,setCar,setQuan,WillMount} from '../../actions/index';

import Head from "../../components/head/index";
import {connect} from "react-redux";

// ES7 语法    @connect 类装饰器  修饰 class ，只能写在class前面，中间不能let 或者var
// 把元数据 装载到 class  里面，mapStateToProps取state值，子组件通过this.props取
@connect(
    state=>{
        return {
            carNum:state.carNum,
            goodsCar:state.goodsCar,
            total:state.total,
            quan:state.quan
        }
    }
)
export default class Car extends Component{

    state={
        dingdan:[]
    }

    componentWillMount(){
        // let {goodsCar}=store.getState();
        let {goodsCar,dispatch} = this.props;
        dispatch(getGoodsCar({
            url:"/vue/mogujiecount",
            username:sessionStorage.username,
            callback:()=>{

            }
        }))
        dispatch(WillMount({
            url:"/vue/mogujiecount",
            username:sessionStorage.username,
        }));
    }

    //组件Checkbox默认有val值,不能再传其他的参数
    // change=(val)=>{
    //    console.log(val);
    //    //val.target:有选中状态和自定义属性dataId
    //    var {checked,dataId} = val.target; //解构取值
    //    //操作哪一条数据就会得到对应的goodid和checked值,发出action,传参，在reducer判断
    //    store.dispatch(setCar({checked,goodid:dataId}))
    // }

    change=(val)=>{
       console.log(val);
       var {checked,dataId} = val.target; 
       let {dispatch} = this.props;
       dispatch(setCar({
           url:"/vue/mogujieupdatecheck",
           username:sessionStorage.username,
           checked,
           goodid:dataId
       }))
    }

    //全选
    checkAll =(val)=>{
        let {checked}=val.target;
        let {dispatch} = this.props;
         dispatch(setQuan({
             url:"/vue/mogujieupdatecheckAll",
             username:sessionStorage.username,
             checked,
         }));
    }

    //-号
    reduce(goodid,count,check){
        let {dispatch} = this.props;
        if(count>1){
            store.dispatch(addReduce({
            url:"/vue/mogujieupdatecar",
            username:sessionStorage.username,
            goodid,
            count:-1,
            callback:()=>{
                
            }
        }))
    }
        //更新购物车角标  
        dispatch(getGoodsCount({
            url:"/vue/mogujiecount",
            username:sessionStorage.username,
            callback:()=>{
                
            }
        }))
        dispatch(setCar({
           url:"/vue/mogujieupdatecheck",
           username:sessionStorage.username,
           checked:check,
           goodid
       }))
    }

    //+号
    add(goodid,count,check){
        let {dispatch} = this.props;
        if(count>=1&&count<10){
            dispatch(addReduce({
            url:"/vue/mogujieupdatecar",
            username:sessionStorage.username,
            goodid,
            count:1,
            callback:()=>{
                
            }
        }))
    }
    //更新购物车角标
        dispatch(getGoodsCount({
            url:"/vue/mogujiecount",
            username:sessionStorage.username,
            callback:()=>{
                
            }
        }))
        dispatch(setCar({
           url:"/vue/mogujieupdatecheck",
           username:sessionStorage.username,
           checked:check,
           goodid
       }))
    }

    //删除单条
    delone(goodid,check){
        let {dispatch} = this.props;
        dispatch(delOne({
            url:"/vue/mogujiedelOne",
            username:sessionStorage.username,
            goodid,
            callback:()=>{
                Toast.success('删除成功!', 1);
            }
        }))
        dispatch(getGoodsCount({
            url:"/vue/mogujiecount",
            username:sessionStorage.username,
            callback:()=>{
                
            }
        }))
        dispatch(setCar({
           url:"/vue/mogujieupdatecheck",
           username:sessionStorage.username,
           checked:check,
           goodid
       }))
    }

    //结算
    jiesuan=()=>{
        //let {goodsCar}=store.getState();
        let {goodsCar,dispatch} = this.props;
        var flag = false;//默认都没选中
        goodsCar.forEach((item, index) => {
            console.log(item.check);
            if (item.check) {  //只要有一个选中就跳转
                flag = true;
                this.setState({
                    dingdan:this.state.dingdan.push(item)
                })
            } 
        });
        if(flag){
            //将选中的商品存入sessionStorage
            sessionStorage.setItem('reactDingdan', JSON.stringify(this.state.dingdan));
            this.props.history.push('/dingdan');
        }else{
            Toast.fail('请选择商品后结算！', 1);
        }
    }


    render(){
        console.log(this.props)
        //let {carNum,goodsCar,total,quan}=store.getState();
        let {carNum,goodsCar,total,quan}=this.props;
        var totalS=0;
        goodsCar.map(item=>{
            if(item.check){
                totalS+=item.price*item.num
            }
        })
        return (
            <div className="box">
                <Head  show="true" history={this.props.history}></Head>
                <div className="bg">
                    <h3>购物车</h3>
                    <p>共{carNum}件宝贝~</p>
                </div>
                <div className="content">
                    {
                        goodsCar&&goodsCar.map((item,index)=>{
                            return (
                                <ul key={index} className="tbody">
                                    <li className="l1"><Checkbox dataId={item.goodid} checked={item.check} onChange={this.change}/></li>
                                    <li className="l2"><img src={item.goodimg} alt=""/></li>
                                    <li className="l3">
                                        <p className="title">{item.goodtitle}</p>
                                        <span className="price">￥{(item.goodprice).toFixed(2)}</span>
                                        <div className="jiajian">
                                            <button className="reduce" onClick={()=>this.reduce(item.goodid,item.num*1,item.check)}>-</button>
                                            <input type="text" value={item.num*1} readOnly/>
                                            <button className="add" onClick={()=>this.add(item.goodid,item.num*1,item.check)}>+</button>
                                        </div>
                                    </li>
                                    <li className="l4"><i className="iconfont icon-shanchu" onClick={()=>this.delone(item.goodid,item.check)}></i></li>
                                </ul>
                            )
                        })
                    }
                </div>
                <div className="foot">
                    <div className="check">
                        <Checkbox checked={quan} onChange={this.checkAll}/>全选
                    </div>
                    <p className="priceTotal">合计：
                        <span >￥{total}</span>元
                    </p>
                    <button type="primary" className="buy" onClick={this.jiesuan}>结算</button>
                </div>
            </div>
        )
    }
}


