
import "./index.scss";
import React, {Component}  from "react";
import {NavLink} from "react-router-dom";
import {Badge} from "antd-mobile";

import store from '../../store';
import {getGoodsCount} from '../../actions/index';


export default class MyFoot extends Component{
    state = {
        foots:[
            {txt:"首页",path:"/app/home",name:"home",icon:"icon-home"},
            {txt:"商品",path:"/app/goods",name:"goods",icon:"icon-goodsfill"},
            {txt:"购物车",path:"/app/car",name:"car",icon:"icon-shopping-cart"},
            {txt:"我",path:"/app/my",name:"mine",icon:"icon-my"},
        ]      
    }

    //得到用户购物车数量
    componentWillMount(){
        store.dispatch(getGoodsCount({
            url:"/vue/mogujiecount",
            username:sessionStorage.username,
            callback:()=>{

            }
        }))
    }

    render(){
        let {carNum}=store.getState();
        return (
            <footer>
                {
                    this.state.foots.map((item,index)=>{
                        return (
                            <div key={index}>
                                <NavLink to={item.path} activeClassName="nav-active">
                                    <i className={"iconfont "+item.icon}></i>
                                    <span>{item.txt}</span>         
                                </NavLink>
                            </div>
                        )
                    })
                }
                <Badge text={carNum} style={{ marginLeft: 12 }} className="carnum"></Badge>
            </footer>
        )
    }
}


