

import "./index.scss";
import React, {Component}  from "react";

import  axios from "axios";
import {Toast,WhiteSpace,WingBlank,Tabs,PullToRefresh,Stepper,Button,Modal} from "antd-mobile";
import {Link} from "react-router-dom";

import store from '../../store';
import {addCar} from '../../actions/index';

import Head from "../../components/head/index";
import MyFoot from "../../components/myfoot/index";


const alert = Modal.alert;

export default class GoodDetail extends Component{

    state={
       val:1 
    }

    onChange=(val)=>{
        this.setState({ val });
        //console.log(val);
    }

    addcar=()=>{
        let {match,history,location}=this.props;
        if(sessionStorage.username){ //发出actions
            store.dispatch(addCar({
                url:"/vue/mogujieaddcar",
                username:sessionStorage.username,
                goodid:match.params.id,
                goodtitle:new URLSearchParams(location.search).get('title'),
                goodimg:new URLSearchParams(location.search).get('img'),
                goodprice:new URLSearchParams(location.search).get('price'),
                addnum:this.state.val,
                callback:()=>{
                    setTimeout(()=>{
                        Toast.success('加入成功', 1);
                    },500) 
                }
            }))
        }else{
            alert('您还未登录!', '确定要登录后购买吗?', [
                { text: '取消', onPress: () => console.log('cancel'), style: 'default' },
                { text: '确定', onPress: () => {history.push('/login')} },
            ]);
        }
    }

    buy=()=>{
        let {match,history,location}=this.props;
        if(sessionStorage.username){ //发出actions
            store.dispatch(addCar({
                url:"/vue/mogujieaddcar",
                username:sessionStorage.username,
                goodid:match.params.id,
                goodtitle:new URLSearchParams(location.search).get('title'),
                goodimg:new URLSearchParams(location.search).get('img'),
                goodprice:new URLSearchParams(location.search).get('price'),
                addnum:this.state.val,
                callback:()=>{
                     history.push("/app/car");
                }
            }))
        }else{
            alert('您还未登录!', '确定要登录后购买吗?', [
                { text: '取消', onPress: () => console.log('cancel'), style: 'default' },
                { text: '确定', onPress: () => {history.push('/login')} },
            ]);
        }
    }

    componentWillMount(){
        
    }

    render(){
        let {match,history,location}=this.props;
        return (
           <div className="box">
               <Head title={new URLSearchParams(location.search).get('title')} show={true} history={this.props.history}></Head>
               <img width="100%" src="//s20.mogucdn.com/b7/pic/151116/16rfcc_ifrgmnzqg4zgcntfgqzdambqmeyde_1920x150.jpg" alt=""/>
               <WingBlank>
               <div className="detail">
                   <h3 className="detailtitle">{new URLSearchParams(location.search).get('title')}</h3>
                   <img width='100%' src={new URLSearchParams(location.search).get('img')} alt=""/>
                   <div className="right"> 
                        <div className="price">
                            <p className="p1">原价：￥{((new URLSearchParams(location.search).get('price'))*1.12).toFixed(2)}</p>
                            <p className="p2">现价：￥{((new URLSearchParams(location.search).get('price'))*1).toFixed(2)}</p>
                        </div>
                        <Stepper
                            style={{ width: '35%', minWidth: '100px' }}
                            showNumber
                            max={10}
                            min={1}
                            value={this.state.val}
                            onChange={this.onChange}
                            />
                            <div className="goto">
                                <button className="car" onClick={this.addcar}>加入购物车</button>
                                <button className="buy" onClick={this.buy}>立即购买</button>
                            </div>
                   </div>
               </div>
               </WingBlank>
           </div>
        )
    }
}


