import "./index.scss";
import React, {Component}  from "react";

import {  WhiteSpace, WingBlank ,Button,List, Toast} from 'antd-mobile';
import Head from "../../components/head";

import mui from './mui.min.js';
import init from './init.js';

import axios from "axios";
//接口代理
//axios.defaults.baseURL = "http://localhost:3200";  // 本地接口
axios.defaults.baseURL = "http://123.56.221.140:3200";  // 线上接口

export default class Login extends Component{

    state={
        username:"",
        pass:"",
        disabled:true
    }

    change=()=>{
        this.setState({
            username:this.refs.username.value,
            pass:this.refs.pwd.value
        })

        let usernameReg=/^[\u4e00-\u9fa5]{2,4}$/ig;
        let pwdReg=/^[a-z0-9]{6,8}$/ig;

        if(usernameReg.test(this.refs.username.value)&&pwdReg.test(this.refs.pwd.value)){
            this.setState({
                disabled:false
            })
            }else{
            this.setState({
                disabled:true
            })
        }
    }

    login(){
        axios.post("/vue/reactLogin",{
            params:{
                username:this.state.username,
                pass:this.state.pass
            }
        }).then(res=>{
            if(res.data.type==1){
                Toast.success('登录成功!', 0.8);
                setTimeout(()=>{
                    this.props.history.push("/app/home");
                    sessionStorage.username=this.state.username
                },800)   
            }else{
                Toast.fail(res.data.msg, 2);
                this.refs.username.value="",
                this.refs.pwd.value=""
            }
        }) 
    }
    
    getAuth=(index)=>{
    	var auths = '';
    	plus.oauth.getServices(
					(services) => {
						auths = services; //支持这四个：QQ，小米，新浪微博，微信
						console.log(JSON.stringify(auths)); //
						var s = auths[index];
						if(!s.authResult) {
					s.login(function(e) {
						alert("登录认证成功！");
						s.getUserInfo(function(e) {
							alert("获取用户信息成功：" + JSON.stringify(s.userInfo));
							console.log(s.userInfo.nickname); //用户信息
						}, function(e) {
							alert("获取用户信息失败：" + e.message + " - " + e.code);
						});
					}, function(e) {
						alert("登录认证失败！");
					});
				} else {
					alert("已经登录认证！");
				}
					},
					(e) => {
						alert("获取分享服务列表失败：" + e.message + " - " + e.code);
					}
				)	
    }
    

    render(){
        return (
            <div className="box">
                <Head title="登录" show={true} history={this.props.history} ></Head>
                <WingBlank>
                    <List>
                        <div className="username">
                        <label htmlFor="">
                        用户名 :<input
                            type="text"
                            ref="username"
                            placeholder="请输入用户名"
                            onChange={this.change}   
                        />
                        </label>
                        </div>

                        <div className="pwd">
                        <label htmlFor="">
                        密  码 :<input
                            type="password"
                            ref="pwd"
                            placeholder="请输入密码"
                            onChange={this.change}
                        />
                        </label>
                        </div> 
                    </List>  
                    <Button type="primary" onClick={()=>{this.login()}} disabled={this.state.disabled}>登录</Button>  
                </WingBlank>  
                <Button type="primary" onClick={()=>this.getAuth(0)} >QQ登录</Button> 
                <Button type="primary" onClick={()=>this.getAuth(2)} >微博登录</Button> 
                <Button type="primary" onClick={()=>this.getAuth(3)} >微信登录</Button>
            </div>
        )
    }
}


