import "./index.scss";
import React, { Component } from "react";
import { Route, Switch, Redirect, Link } from "react-router-dom";
import { Grid, WingBlank, NoticeBar, WhiteSpace,Flex } from "antd-mobile";

import axios from "axios";
import store from "../../store";
import {getGoods} from "../../actions/index";

import Head from "../../components/head/index";
import Guide from "../guide/index";


let Array = [
  {
    img:
      "https://s10.mogucdn.com/mlcdn/c45406/181031_20c98cekk381c5i3ladb506e8g290_172x172.png",
    name: "上衣"
  },
  {
    img:
      "https://s10.mogucdn.com/mlcdn/c45406/181031_5jbbcl9ja0hdg8di6j0b4hfaa8iba_172x172.png",
    name: "裙装"
  },
  {
    img:
      "https://s10.mogucdn.com/mlcdn/c45406/181031_522dgj6e1i9ji87gkc4hcbcc9cb2b_172x172.png",
    name: "裤子"
  },
  {
    img:
      "https://s10.mogucdn.com/mlcdn/c45406/181031_353i8ki02gfl955alfi9k3i9j4i5h_172x172.png",
    name: "女鞋"
  },
  {
    img:
      "https://s10.mogucdn.com/mlcdn/c45406/181031_1fa6a0aj703ek9g6kkc5ek5495ke1_172x172.png",
    name: "包包"
  },
  {
    img:
      "https://s10.mogucdn.com/mlcdn/c45406/181031_75gbbae792188710cgb83bd3j1fhj_172x172.png",
    name: "配饰"
  },
  {
    img:
      "https://s10.mogucdn.com/mlcdn/c45406/181031_5i7idgb59209bhkflgc4j9c4cb87b_172x172.png",
    name: "美妆"
  },
  {
    img:
      "https://s10.mogucdn.com/mlcdn/c45406/181031_7i219f47fc0ki19f58k5di58f8ae7_172x172.png",
    name: "家居"
  },
  {
    img:
      "https://s10.mogucdn.com/mlcdn/c45406/181031_56ldahcgfcb306ieceh0f36edga0k_172x172.png",
    name: "内衣"
  }
];

const data = Array.map((item, i) => ({
  icon: item.img,
  text: item.name
}));

export default class Home extends Component {

  componentWillMount(){
    store.dispatch(getGoods({
      url:"/vue/mogujie",
      key:"靴子",
      callback:()=>{

      }
    }))
  }

  render() {
    let {goods}=store.getState();
    return (
      <div className="box">
        <Head title="首页" show="true" history={this.props.history} />
        <section>
          <NoticeBar
            marqueeProps={{ loop: true, style: { padding: "0 7.5px" } }}
          >
             温馨提示：本店商品质量有保证，全部正品提供，默认快递为申通和中通，下单时请备注留言，谢谢~
          </NoticeBar>
          <h1>GUMO</h1>
          <Guide history={this.props.history} />
          <h3>购物商城</h3>
          <WingBlank>
            <Link to={"/app/goods"}>
              <Grid data={data} columnNum={3} />
            </Link>
          </WingBlank>
          <h3>热门推荐</h3>
          <div className="xiu">
          {
            goods.map((item,index)=>{
              return (           
                <ul key={index} className="list">
                  <li>
                    <Link to={"/goods/detail/"+item.id+"?title="+item.title+"&img="+item.img+"&price="+item.price}>
                      <img src={item.img} alt=""/>
                    </Link>
                    <p className="desc">{item.title}</p>
                    <span className="price">￥{(item.price).toFixed(2)}元</span>
                  </li>
                </ul>
              )
            })
          }
           </div>
        </section>
      </div>
    );
  }
}



