import "./index.scss";
import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import { NavBar, Icon ,ActionSheet,Toast } from "antd-mobile";

import store from '../../store';
import axios from 'axios';
import {getGoods} from '../../actions/index';


export default class MyHead extends Component {

  search=()=>{
    store.dispatch(getGoods({
      url:"/vue/mogujie",
      key:this.refs.search.value,
      callback:()=>{
        
      }
    }))
  }

  render() {
    let {goods}=store.getState();
    return (
      <div>
        <div className="sousuo">
          <img src="https://s10.mogucdn.com/mlcdn/c45406/181016_143l3ehl4ebad6c2326gjk6d4h41g_48x48.png" alt=""/>
          <input ref='search' type="text" placeholder="分类搜索"/>
          <div onClick={this.search} className="search"><img src="https://s10.mogucdn.com/mlcdn/c45406/181205_2llkjh1g0fe27h51ahh5k5f27gkk9_116x96.png" alt=""/></div>
        </div>
      </div>
    );
  }
}



