import {GETGOODS,ADDCAR,GETGOODSCOUNT,GETGOODSCAR,ADDREDUCE,DELONE} from "../actions/index";

const defaultState = {
    goods:[],
    tabs:[
        { title: '上衣' },
        { title: '裙装' },
        { title: '女鞋' },
        { title: '美妆' },
        { title: '内衣' },
        { title: '包包' },
        { title: '配饰' },
    ],
    addcarmsg:"",
    carNum:0,
    goodsCar:[],
    total:0, //总价
    quan:false, //全选状态
    dingdan:[]
}

export default (state=defaultState,action)=>{
    console.log(action);

    switch (action.type){
        case GETGOODS:
        return {...state,goods:action.goods};
        break;

        case ADDCAR:
        return {...state,addcarmsg:action.addcarmsg};
        break;

        case GETGOODSCOUNT:
        return {...state,carNum:action.carNum};
        break;

        case GETGOODSCAR:
        return {...state,goodsCar:action.goodsCar};
        break;

        case ADDREDUCE:
        return {...state,goodsCar:action.goodsCar};
        break;

        case DELONE:
        return {...state,goodsCar:action.goodsCar};
        break;

        case "setCar": 
        //arr是选中商品的集合
        var arr  = state.goodsCar.map((item)=>{
            //如果原数组的goodid等于action传过来的goodid，改变数组check值
            if(item.goodid==action.goodid){
                item.check = action.checked;
            }
            return item;
        });
        var total = 0;
        arr.forEach((item)=>{  //求总和
            if(item.check){ //这个判断是为了动态获取总价
                total+=item.goodprice*item.num;
            }
        })
        //判断全选，只要有一个未选中，全选就为false
        var flag = true; //默认全选选中
        arr.forEach((item)=>{
            if(!item.check){
                flag = false;
            }
        })
        return {...state,goodsCar:arr,total:total.toFixed(2),quan:flag};
        break;
        
        //全选操作
        case "setQuan":
        var total = 0;
        let arr  = state.goodsCar.map(item=>{
            item.check = action.checked; //每个单选状态都和全选一致
            if(item.check){
                total+=item.goodprice*item.num;
            }
            return item;
        })
        return {...state,quan:action.checked,goodsCar:arr,total:total.toFixed(2)}
        break;

        case "WillMount":
        var total = 0;
        console.log(action.goodsCar)
        action.goodsCar.map((item,index)=>{
            if(item.check){
                total+=item.goodprice*item.num;
            }
            return item;
        })
        var flag = true; //默认全选选中
        action.goodsCar.forEach((item)=>{
            if(!item.check){
                flag = false;
            }
        })
        return {...state,total:total.toFixed(2),quan:flag};
        break;

        default:
        return state;
        break;
    }
}


