import "./index.scss";
import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import { NavBar, Icon ,ActionSheet,Toast } from "antd-mobile";

export default class Head extends Component {
  constructor() {
    super();
    this.state = {
      clicked: "none",
      clicked1: "none",
      clicked2: "none"
    };
  }

  dataList = [
    { url: "OpHiXAcYzmPQHcdlLFrc", title: "发送给朋友" },
    { url: "wvEzCMiDZjthhAOcwTOu", title: "新浪微博" },
    { url: "cTTayShKtEIdQVEMuiWt", title: "生活圈" },
    { url: "umnHwvEgSyQtXlZjNJTt", title: "微信好友" },
    { url: "SxpunpETIwdxNjcJamwB", title: "QQ" }
  ].map(obj => ({
    icon: (
      <img
        src={`https://gw.alipayobjects.com/zos/rmsportal/${obj.url}.png`}
        alt={obj.title}
        style={{ width: 36 }}
      />
    ),
    title: obj.title
  }));
  showShareActionSheet = () => {
    ActionSheet.showShareActionSheetWithOptions(
      {
        options: this.dataList,
        // title: 'title',
        message: "分享"
      },
      buttonIndex => {
        this.setState({
          clicked1:
            buttonIndex > -1 ? this.dataList[buttonIndex].title : "cancel"
        });
        // also support Promise
        return new Promise(resolve => {
        //   Toast.info("closed after 1000ms");
          setTimeout(resolve, 1000);
        });
      }
    );
  };

  gotoSearch = () => {
    let { history } = this.props;
    history.push("/search/");
  };

  search=()=>{
    this.props.history.push('/search');
  }

  render() {
    let { title, show, history } = this.props;
    return (
      <NavBar
        mode="light"
        icon={<Icon type="left" /> }
        onLeftClick={show ? () => history.go(-1) : () => console.log("")}
        rightContent={[
          <Icon key="0" type="search" style={{ marginRight: '16px' }} onClick={this.search}/>,
          <Icon onClick={this.showShareActionSheet} key="1" type="ellipsis" />
        ]}
      >
        {title}
      </NavBar>
    );
  }
}



