

import "./index.scss";
import React, {Component}  from "react";

import  axios from "axios";
import {Toast,WhiteSpace,WingBlank,Tabs,PullToRefresh,List, Badge} from "antd-mobile";
import {Link} from "react-router-dom";

export default class GoodList extends Component{


    state = {
        refreshing:false  // 没有在刷新 
    }

    componentWillMount(){
        
    }

    render(){
        let {tab,data,reverseGoods} = this.props;
        let myData = data&&data.filter((item,index)=>item.type==tab.title);
        return (
           <div>
               <PullToRefresh
                    damping={60}
                    ref="scrolling"
                    style={{
                        overflow: 'auto',
                    }}
                    indicator={{ deactivate: '下拉刷新' }}
                    direction={'down'}
                    refreshing={this.state.refreshing}
                    onRefresh={() => {
                        this.setState({ refreshing: true });  // 表示正在刷新
                        setTimeout(() => {
                            reverseGoods();
                            this.setState({ refreshing: false });  // 刷新完成
                        }, 2000);
                    }}
                >
               {
                   myData.map((item,index)=>{
                       return ( 
                        <div className="moveIn" key={index} style={{width:'100%'}}>
                            <Link to={"/goods/detail/"+item.id+"?title="+item.title+"&img="+item.img+"&price="+item.price}>
                                <img width='50%' height='100%' src={item.img} alt=""/>
                            </Link>
                            <div className="right">
                                <Link to={"/goods/detail/"+item.id+"?title="+item.title+"&img="+item.img+"&price="+item.price}>
                                    <h4>{item.title}</h4>
                                </Link>
                                <p className="p1">原价 : ￥{(item.price*1.12).toFixed(2)}</p>
                                <p className="p2">优惠价 : ￥{(item.price).toFixed(2)}</p>
                                <div className="cu">
                                    <Badge text="减" hot style={{ marginLeft: 12 }} />
                                    <Badge text="惠" hot style={{ marginLeft: 12 }} />
                                    <Badge text="免" hot style={{ marginLeft: 12 }} />
                                    <Badge text="反" hot style={{ marginLeft: 12 }} />
                                </div>
                            </div>  
                        </div>
                       )
                   })
               }
             </PullToRefresh>
           </div>
        )
    }
}


