
import axios from "axios";
//接口代理
//axios.defaults.baseURL = "http://localhost:3200";  // 本地接口
axios.defaults.baseURL = "http://123.56.221.140:3200";  // 线上接口

//得到全部商品
export const GETGOODS="getGoods";
export const getGoods=({url,key,callback})=>{
    return axios.get(url,{
        params:{
            key
        }
    }).then(res=>{
        callback()
        return {
            type:GETGOODS,
            goods:res.data.result
        }
       
    })
}

//加入购物车
export const ADDCAR="addcar";
export const addCar=({url,username,goodid,goodtitle,goodimg,goodprice,addnum,callback})=>{
    return axios.get(url,{
        params:{
            username,
            goodid,
            goodtitle,
            goodimg,
            goodprice,
            addnum
        }
    }).then(res=>{   
        callback()
        return {
            type:ADDCAR,
            addcarmsg:res.data.result.msg
        } 
    })
}

//用户购物车数量
export const GETGOODSCOUNT="getGoodsCount";
export const getGoodsCount=({url,username,callback})=>{
    return axios.get(url,{
        params:{
           username 
        }
    }).then(res=>{
        var list=res.data.result;
        var sum=0;
        list.map((item)=>{
            sum+=item.num;
        })
        callback()
        return {
            type:GETGOODSCOUNT,
            carNum:sum
        }   
    })
}

