

import "./index.scss";
import React, {Component}  from "react";
import {Route, Switch,Redirect} from "react-router-dom";
import {Badge,Stepper,Checkbox,Toast,DatePicker,List } from "antd-mobile";

import store from '../../store';
import {getGoodsCar,addReduce,getGoodsCount,delOne,setCar,setQuan,dingDan} from '../../actions/index';

import Head from "../../components/head/index";


import enUs from 'antd-mobile/lib/date-picker/locale/en_US';

const nowTimeStamp = Date.now();
const now = new Date(nowTimeStamp);
// GMT is not currently observed in the UK. So use UTC now.
const utcNow = new Date(now.getTime() + (now.getTimezoneOffset() * 60000));

// Make sure that in `time` mode, the maxDate and minDate are within one day.
let minDate = new Date(nowTimeStamp - 1e7);
const maxDate = new Date(nowTimeStamp + 1e7);
// console.log(minDate, maxDate);
if (minDate.getDate() !== maxDate.getDate()) {
  // set the minDate to the 0 of maxDate
  minDate = new Date(maxDate.getFullYear(), maxDate.getMonth(), maxDate.getDate());
}

function formatDate(date) {
  /* eslint no-confusing-arrow: 0 */
  const pad = n => n < 10 ? `0${n}` : n;
  const dateStr = `${date.getFullYear()}-${pad(date.getMonth() + 1)}-${pad(date.getDate())}`;
  const timeStr = `${pad(date.getHours())}:${pad(date.getMinutes())}`;
  return `${dateStr} ${timeStr}`;
}

// If not using `List.Item` as children
// The `onClick / extra` props need to be processed within the component
const CustomChildren = ({ extra, onClick, children }) => (
  <div
    onClick={onClick}
    style={{ backgroundColor: '#fff', height: '45px', lineHeight: '45px', padding: '0 15px' }}
  >
    {children}
    <span style={{ float: 'right', color: '#888' }}>{extra}</span>
  </div>
);


export default class DingDan extends Component{

    state = {
        date: now,
        time: now,
        utcDate: utcNow,
        dpValue: null,
        customChildValue: null,
        visible: false,
    }

    submit=()=>{
        this.props.history.push('/fukuan')
    }

    render(){
        let dingdanGoods=JSON.parse(sessionStorage.getItem('reactDingdan'));
        let total=0;
        let countTotal=0;
        dingdanGoods.map(item=>{
            total+= item.num*item.goodprice;
            countTotal+=item.num
        })
        return (
            <div className="box">
               
                <Head title="确认订单" show="true" history={this.props.history}></Head>
                {/*<h3 className="shouhuo">收货人信息</h3>*/}

                <h3 className="qingdan">购物清单</h3>
                <div className="content1">
                    {
                        dingdanGoods&&dingdanGoods.map((item,index)=>{
                            return (
                                <ul key={index} className="ul">
                                    <li className="l5"><img src={item.goodimg} alt=""/></li>
                                    <li className="l6">
                                        <p className="title">{item.goodtitle}</p>
                                        <span className="price">￥{(item.goodprice).toFixed(2)}</span>
                                        <div className="jiajian"> 
                                            <input type="text" value={item.num*1} readOnly/>
                                        </div>
                                    </li>
                                </ul>
                            )
                        })
                    }
                    <p className="peisong">
                        <span className="span1">配送方式</span>
                        <span className="span2">快递 免邮</span>
                    </p>
                    <DatePicker
                        mode="date"
                        title="请选择送货时间"
                        extra="Optional"
                        value={this.state.date}
                        onChange={date => this.setState({ date })}
                        >
                        <List.Item arrow="horizontal">请选择送货时间</List.Item>
                    </DatePicker>
                    <p className="yunfeixian">
                        <span className="span3">运费险</span>
                        <span className="span4">确认收货前退货可赔付9元</span>
                        <Checkbox></Checkbox>
                    </p>
                    <p className="liuyan">
                        <span className="span5">买家留言：</span>
                        <textarea name="" id="" placeholder="选填：填写内容已和卖家协商确认"></textarea>
                    </p>
                    <p className="countTotal">共<span style={{'color':'red'}}>{countTotal}</span>件商品</p>
                </div>
                <div className="dingdanfoot">
                    <p className="pricetotal">
                        合计：<span style={{'color':'red'}}>￥{total.toFixed(2)}</span>元
                    </p>
                    <button type="primary" className="dingdan" onClick={this.submit}>提交订单</button>
                </div>
            </div>
        )
    }
}


