

import "./index.scss";
import React, {Component}  from "react";

import Head from "../../components/head"
import { SearchBar, WhiteSpace, WingBlank ,Badge} from 'antd-mobile';
import {Link} from "react-router-dom";

import {getGoods} from '../../actions/index';
import store from '../../store';


export default class SearchDemo extends Component{

    search=(val)=>{
        store.dispatch(getGoods({
        url:"/vue/mogujie",
        key:val,
        callback:()=>{
            
        }
        }))
    }

    render(){
        let {goods}=store.getState();
        return (
            <div className="box">
                <Head title="搜索商品"  show={true} history={this.props.history}  ></Head>
                <WingBlank>
                    <WhiteSpace/>
                    <SearchBar placeholder="Search" maxLength={8} onChange={this.search}/>  
                </WingBlank>
                   {
                   goods.map((item,index)=>{
                       return ( 
                        <div className="moveIn" key={index} style={{width:'100%'}}>
                            <Link to={"/goods/detail/"+item.id+"?title="+item.title+"&img="+item.img+"&price="+item.price}>
                                <img width='50%' height='100%' src={item.img} alt=""/>
                            </Link>
                            <div className="right">
                                <Link to={"/goods/detail/"+item.id+"?title="+item.title+"&img="+item.img+"&price="+item.price}>
                                    <h4>{item.title}</h4>
                                </Link>
                                <p className="p1">原价 : ￥{(item.price*1.12).toFixed(2)}</p>
                                <p className="p2">优惠价 : ￥{(item.price).toFixed(2)}</p>
                                <div className="cu">
                                    <Badge text="减" hot style={{ marginLeft: 12 }} />
                                    <Badge text="惠" hot style={{ marginLeft: 12 }} />
                                    <Badge text="免" hot style={{ marginLeft: 12 }} />
                                    <Badge text="反" hot style={{ marginLeft: 12 }} />
                                </div>
                            </div>  
                        </div>
                       )
                   })
               }          
            </div>
        )
    }
}


