import React, {Component}  from "react";
import {HashRouter as Router ,Route,Switch,Redirect} from "react-router-dom";

import Login from "./login/index";
import Register from "./register/index";
import App from "./app/index";
import Detail from "./goodDetail/index";
import DingDan from "./dingdan/index";
import Search from "./search/index";
import FuKuan from "./fukuan/index";

export default class Layout extends Component{
    render(){
        return (
            <Router>
                <Switch>
                    <Route path='/' exact component={App}></Route>
                    <Route path='/register' component={Register}></Route>
                    <Route path='/login' component={Login}></Route>
                    <Route path='/app' component={App}></Route>
                    <Route path='/goods/detail/:id' component={Detail}></Route>
                    <Route path='/dingdan' component={DingDan}></Route>
                    <Route path='/search' component={Search}></Route>
                    <Route path='/fukuan' component={FuKuan}></Route>
                    <Route render={
                        ()=>{
                            return <Redirect to="/app/home"></Redirect>
                        }
                    }></Route>
                </Switch>
            </Router>
        )
    }
}


