import React, { Component } from "react";

//引入swiper的js，css在index.scss中引入
import "../utils/swiper/swiper-3.3.1.min.js";

//第一步：引入js和css，第二步：创建静态结构，第三步：实例化
export default class SwiperDemo extends Component {
  render() {
    let { id, swiperOptions } = this.props;
    return (
      <div>
        <div className="swiper-container" id={id}>
          <div className="swiper-wrapper">
            {this.props.children.map((item, index) => {
              return (
                <div className="swiper-slide" key={index}>
                  {item}
                </div>
              );
            })}
          </div>
          <div className="swiper-pagination" />
        </div>
      </div>
    );
  }

  //如果父组件传过来的数据是死的，在componentDidMount这里实例化
  componentDidMount() {
    let { id, swiperOptions } = this.props;
    if (this.props.children.length > 0) {
      new Swiper("#" + id, swiperOptions);
    }
  }

  //实例化swiper(不能和组件名重复),如果是活数据，在componentDidUpdate这里实例化，
  componentDidUpdate() {
    let { id, swiperOptions } = this.props;
    if (this.props.children.length > 0) {
      new Swiper("#" + id, swiperOptions);
    }
  }
}



