import "./index.scss";
import React, {Component}  from "react";

import {  WhiteSpace, WingBlank ,Button,List, Toast} from 'antd-mobile';
import Head from "../../components/head";

import axios from "axios";
//接口代理
//axios.defaults.baseURL = "http://localhost:3200";  // 本地接口
axios.defaults.baseURL = "http://123.56.221.140:3200";  // 线上接口

export default class Register extends Component{

    state={
        username:"",
        pass:"",
        disabled:true
    }

    change=()=>{
        this.setState({
            username:this.refs.username.value,
            pass:this.refs.pwd.value
        })

        let usernameReg=/^[\u4e00-\u9fa5]{2,4}$/ig;
        let pwdReg=/^[a-z0-9]{6,8}$/ig;

        if(usernameReg.test(this.refs.username.value)&&pwdReg.test(this.refs.pwd.value)){
            this.setState({
                disabled:false
            })
            }else{
            this.setState({
                disabled:true
            })
        }
    }

    register(){
        axios.post("/vue/reactRegister",{
            params:{
                username:this.state.username,
                pass:this.state.pass
            }
        }).then(res=>{
            if(res.data.type==1){
                Toast.success('注册成功!', 0.8);
                setTimeout(()=>{
                    this.props.history.push("/login");
                },800)   
            }else{
                Toast.fail(res.data.msg, 2);
                this.refs.username.value="",
                this.refs.pwd.value=""
            }
        }) 
    }

    render(){
        return (
            <div className="box">
                <Head title="注册" show={true} history={this.props.history} ></Head>
                <WingBlank>
                    <List>
                        <div className="username">
                        <label htmlFor="">
                        用户名 :<input
                            type="text"
                            ref="username"
                            placeholder="请输入2-4位中文名"
                            onChange={this.change}   
                        />
                        </label>
                        </div>

                        <div className="pwd">
                        <label htmlFor="">
                        密  码 :<input
                            type="password"
                            ref="pwd"
                            placeholder="请输入6-8位由字母数字组成的密码"
                            onChange={this.change} 
                        />
                        </label>
                        </div> 
                    </List>  
                    <Button type="primary" onClick={()=>{this.register()}} disabled={this.state.disabled}>注册</Button>  
                </WingBlank>       
            </div>
        )
    }
}


