import React, {Component}  from "react";

import SwiperDemo from "../../components/swiper";//导入轮播图组件

import "./index.scss";//改变图片大小

export default class Guide extends Component{
    state={
        imgs:[
            require("../../../assets/imgs/1.webp"),
            require("../../../assets/imgs/2.webp"),
            require("../../../assets/imgs/3.webp"),
            require("../../../assets/imgs/4.webp"),
            require("../../../assets/imgs/5.webp"),
            require("../../../assets/imgs/6.webp"),
        ]
    }

    //点击最后一张图片进入app
    gotoGoods(){
        this.props.history.push("/app/goods");
    }

    render(){
        let img=this.state.imgs.map((item,index)=>{
            return (
                <img onClick={()=>this.gotoGoods()} className="img" src={item} alt=""  key={index} />
            )
        })
        return (
            <div>
                <SwiperDemo id="guide" swiperOptions={{speed:1000,loop:true,autoplay:2000,pagination: ".swiper-pagination",paginationClickable: true}}>
                    {img}
                </SwiperDemo>
            </div>
        )
    }
}


