

import "./index.scss";
import React, {Component}  from "react";
import {Route, Switch,Redirect} from "react-router-dom";
import { Tabs, WhiteSpace, Badge,Toast } from 'antd-mobile';

import axios from "axios";
import store from "../../store";
import {getGoods} from "../../actions/index";

import MyHead from "../../components/myhead/index";
import GoodList from "../goodList/index";



export default class Goods extends Component{

    // state={
    //      goods:[],
    //      tabs:[
    //         { title: '上衣' },
    //         { title: '裙装' },
    //         { title: '女鞋' },
    //         { title: '美妆' },
    //     ]
    // }

    //反转商品顺序
    reverseGoods=()=>{
        let {goods}=store.getState();
        this.setState({
            goods:goods.reverse()
        })
    }

    //请求数据
    componentWillMount(){
         Toast.loading('加载中...', 0.6, () => {
            
        });
        //发出action
        setTimeout(()=>{
            store.dispatch(getGoods({
                url:"/vue/mogujie",
                key:"",
                callback:()=>{
                    Toast.hide();
                    // Toast.info("请求成功");
                }
            }))
        },600)
    }

       
    render(){
        let {tabs,goods}=store.getState();
        return (
            <div className="box">
                <MyHead></MyHead>
                <img width="100%" src="//s20.mogucdn.com/b7/pic/151116/16rfcc_ifrgmnzqg4zgcntfgqzdambqmeyde_1920x150.jpg" alt=""/>
                <Tabs tabs={tabs} initialPage={1} >
                   {
                       tabs.map((item,i)=>{
                           return (
                               <GoodList reverseGoods={this.reverseGoods} tab={item} data={goods} key={i}></GoodList>
                           )
                       })
                   }
                </Tabs>
                
            </div>
        )
    }
}


